<template>
    <div class="slidercontainer">
        <Slider :images="images" :intervalDuration="slideDuration"/>
    </div>
</template>
<script>
    import Slider from "vue-slider-rw";
    export default {
        components :{
            Slider
        },
        data () {
            return {
                images : [
                    {id: 0,url : "/static/images/carousal/image1.jpg",title: "Lightning"},
                    {id: 1,url : "/static/images/carousal/image2.jpg",title: "Batman on the roof"},
                    {id: 2,url : "/static/images/carousal/image3.jpg",title: "Suicide Squad"},
                    {id: 3,url : "/static/images/carousal/image4.jpg",title: "Flash & Reverse Flash"},
                    {id: 4,url : "/static/images/carousal/image5.jpg",title: "Batman Red"}
                ],
                slideDuration : 4000
            }
        }
    }
</script>
<style>
    .slidercontainer{
        height:500px;
        width:100%;
    }
</style>


